<template>
  <div class="dashboard">
    <el-row>
      <el-col :span="24">
        <div id="gotobedbar"></div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import echarts from 'echarts';
  import data from '../../static/data/data.json';
  export default {
    data() {
      return {
        chart: null
      };
    },
    methods: {
      getBeforeDate(n){
        var list = [];
        var d = new Date(); // 2012229
        var s = '';
        var i = 0;
        while (i < n) {
          d.setDate(d.getDate() - 1);
          var year = d.getFullYear();
          var mon = d.getMonth() + 1;
          var day = d.getDate();
          list.push(year + "-" + (mon < 10 ? ('0' + mon) : mon) + "-" + (day < 10 ? ('0' + day) : day));
          i++;
        }
        return list.reverse();
      },
      drawbar(id) {
        let o = document.getElementById(id);
        let height = document.documentElement.clientHeight;
        height -= 120;
        o.style.height= height+"px";
        this.chart = echarts.init(o);
        var option = {
          title: {
            text: '',
            subtext: '',
            left:'center',
          },
          tooltip: {
            trigger: 'axis',
            axisPointer : {            // 
              type : 'shadow'        // 'line' | 'shadow'
            }
          },
          legend: {
            data: ['1', '2', '3', '4'],
            orient:'vertical',
            left:'right',
            top:'middle',// top 'top', 'middle', 'bottom'
            itemGap:20
          },
          toolbox: {
            show: true,
            orient: 'horizontal',      // 
            // 'horizontal'  'vertical'
            x: 'right',                // 
                                       // 'center'  'left'  'right'
                                       //  {number}xpx
            y: 'top',                  // 
                                       // 'top'  'bottom'  'center'
                                       //  {number}ypx
            color: ['#1e90ff', '#22bb22', '#4b0082', '#d2691e'],
            feature: {
              mark: {show: true},
              dataView: {show: true, readOnly: false},
              magicType: {show: true, type: ['line', 'bar', 'stack', 'tiled']},
              restore: {show: true},
              saveAsImage: {show: true}
            }
          },
          calculable: true,
          dataZoom: {
            show: true,
            realtime: true,
            start: 50,
            end: 100
          },
          xAxis: [
            {
              type: 'category',
              boundaryGap: false,
              data: this.getBeforeDate(30)
            }
          ],
          yAxis: [
            {
              type: 'value'
            }
          ],
          series: [
            {
              name: '1',
              type: 'line',
              tiled : '',
              areaStyle: {normal: {}},
              data: function () {
                var list = [];
                for (var i = 1; i <= 30; i++) {
                  list.push(Math.round(Math.random() * 3000));
                }
                return list;
              }()
            },
            {
              name: '2',
              type: 'line',
              tiled : '',
              areaStyle: {normal: {}},
              data: function () {
                var list = [];
                for (var i = 1; i <= 30; i++) {
                  list.push(Math.round(Math.random() * 800));
                }
                return list;
              }()
            },
            {
              name: '3',
              type: 'line',
              tiled : '',
              areaStyle: {normal: {}},
              data: function () {
                var list = [];
                for (var i = 1; i <= 30; i++) {
                  list.push(Math.round(Math.random() * 200));
                }
                return list;
              }()
            },
            {
              name: '4',
              type: 'line',
              tiled : '',
              areaStyle: {normal: {}},
              data: function () {
                var list = [];
                for (var i = 1; i <= 30; i++) {
                  list.push(Math.round(Math.random() * 100));
                }
                return list;
              }()
            }
          ]
        };
        this.chart.setOption(option);
      }
    },
    mounted() {
      this.$nextTick(function () {
        this.drawbar('gotobedbar');
        var that = this;
        var resizeTimer = null;
        window.onresize = function () {
          if (resizeTimer) clearTimeout(resizeTimer);
          resizeTimer = setTimeout(function () {
            that.drawbar('gotobedbar');
          }, 100);
        }
      });
    }
  }
</script>

<style scoped>
  .wrapper.fixed {
    background: url("../../static/img/Bitmap.5ad3457.png");
    background-size: 100% 100%;
  }

  #gotobedbar {
    width: 100%;
    display: inline-block;
    min-height: 500px;
    margin-right: 15px;
  }
</style>



